module.exports = ".root__container {\n  width: 100vw;\n  height: 100vh;\n  top: 0;\n  left: 0;\n  display: grid;\n  grid-template-columns: auto;\n  grid-template-rows: 0.5fr auto;\n  position: absolute;\n}\n\n.main__header {\n  width: 100%;\n  display: grid;\n  grid-template-columns: 0.75fr 0.75fr 0.75fr;\n  grid-template-rows: 1fr;\n  box-shadow: 0.8rem 0.8rem 2rem rgba(0, 0, 255, 0.14);\n  height: 6rem;\n  margin: 0;\n  text-align: center;\n  align-items: center;\n  transition: background-color 500ms linear;\n  -webkit-animation: fadein 1s ease-in-out 0ms 1;\n          animation: fadein 1s ease-in-out 0ms 1;\n}\n\n.main__header-text {\n  text-transform: uppercase;\n  letter-spacing: 0.1rem;\n  display: inline;\n  margin: 0.5rem 0;\n  font-weight: 900;\n}\n\n.main__container-bg {\n  height: 100%;\n  width: 100%;\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: -2;\n  opacity: 0;\n  background: white;\n  transition: opacity 300ms linear;\n}\n\n.main__footer {\n  background: transparent;\n  position: absolute;\n  bottom: 1rem;\n  left: 1.5rem;\n  z-index: 100;\n}\n\n.main__footer-text {\n  letter-spacing: 0.1rem;\n  color: white;\n}\n\n/*\n  ##Device = Low Resolution Tablets, Mobiles (Landscape)\n  ##Screen = B/w 481px to 767px\n*/\n\n@media (min-width: 481px) and (max-width: 767px) {\n  .main__header {\n    grid-template-columns: 1fr;\n  }\n}\n\n/*\n  ##Device = Most of the Smartphones Mobiles (Portrait)\n  ##Screen = B/w 320px to 479px\n*/\n\n@media (min-width: 320px) and (max-width: 480px) {\n  .main__header {\n    grid-template-columns: 1fr;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"